"use client";

import { useQuran } from "@/hooks/useQuran";
import { Card } from "@/components/ui/card";
import Link from "next/link";
import { Skeleton } from "@/components/ui/skeleton";

import { SurahList as SurahType } from "@/hooks/useQuran";

export default function SurahList() {
  const { data, loading, error } = useQuran<SurahType[]>("/surah");

  if (error) {
    return (
      <div className="text-center p-8">
        <p className="text-red-500">Error: {error}</p>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {[...Array(10)].map((_, i) => (
          <Card key={i} className="p-6">
            <Skeleton className="h-6 w-3/4 mb-4" />
            <Skeleton className="h-4 w-1/2" />
          </Card>
        ))}
      </div>
    );
  }

  const surahs = data?.data ?? [];

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {surahs.map((surah: SurahType) => (
        <Link key={surah.number} href={`/surah/${surah.number}`}>
          <Card className="p-6 hover:shadow-lg transition-shadow cursor-pointer group">
            <div className="flex justify-between items-start mb-2">
              <h2 className="text-xl font-semibold group-hover:text-blue-600 transition-colors">
                {surah.englishName}
              </h2>
              <span className="text-sm text-neutral-500 font-medium">
                {surah.number}
              </span>
            </div>
            <p className="text-neutral-600">{surah.englishNameTranslation}</p>
            <div className="flex justify-between items-center mt-4 text-sm text-neutral-500">
              <span>{surah.revelationType}</span>
              <span>{surah.numberOfAyahs} verses</span>
            </div>
          </Card>
        </Link>
      ))}
    </div>
  );
}
